body{
    margin: 0;
    padding: 0;
    overflow: hidden;
}
.main{
    width: 100vw;
    height: 100vh;
    display: flex;
    background-color: rgba(0, 0, 0,0.6);
    align-items: center;
    justify-content: center;
}
.main-content{
    width: 90%;
    height: 90%;
    display: grid;
    grid-template-columns: 1fr 4fr;
    grid-template-rows: 1fr 5fr;
    column-gap: 7vw;
    grid-template-areas: 
    "main main"
    "profile box";
}
.main-title-box{
    grid-area: main;

    font-family: MaplestoryOTFBold;
    color: rgb(247, 244, 244);
    display: flex;
    justify-content: center;
    align-items: center;
}
.main-title-box>div{
    font-size: 50px;
    width: 400px;
    height: 50px;
    /* animation: scaleXAnimation 2s infinite; */
}
.main-profile{
    background-color: rgba(255,255,255,0.5);
    grid-area: profile;
}
.main-box{
    grid-area: box;
}
@keyframes scaleXAnimation {
    0%, 100% {
      transform: scaleX(1);
    }
    50% {
      transform: scaleX(1.5);
    }
}